body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.central.slide .slide-content {
	display:-ms-flexbox;
	display:flex;
}

.slide-content > * {
	-ms-flex-preferred-size: 50%;
	    flex-basis: 50%;
	z-index:1;
	position:relative;
}

.slide-content > h1, .full {
	-ms-flex-preferred-size: 100%;
	    flex-basis: 100%;
}

.central.slide p {
	padding-left:5%;
}

span.bold {
	font-weight:bold;
}

span.underline {
	text-decoration: underline;
}

p.full {
	text-align:center;
}

.white {
	color:white;
}

.nobullet {
	list-style-type: none;
}

.slide-content img.right-bottom {
	position:absolute;
	bottom:0px;
	right:0px;
	z-index:0;
}

.huge {
	font-size: 144pt;
	text-align:center;
}


